@use 'sass:map';
@use 'sass:meta';
@use '../base/functions' as *;

@mixin povh($povh) {
	position: relative;
	overflow: hidden;
}

@mixin heading-base() {
	line-height: 1.25;
	font-weight: 700;
	color: $heading-color;
	font-family: $font-family-secondary;
}

@mixin button-base() {
	padding: $button-padding-vertical $button-padding-horizontal;
	display: inline-block;
	// min-width: 256px;
	font-family: $font-family-secondary;
	font-size: $button-font-size;
	font-weight: 700;
	line-height: $button-line-height;
	border-radius: $button-border-radius;
	text-align: center;
	text-decoration: none;
	cursor: pointer;
	transition: $button-transition;
	appearance: none;
	outline: none;
}

@mixin button-variant($bg-color, $text-color: $button-text-color) {
	@include button-base();
	background-color: $bg-color;
	color: $text-color;
	border: 1px solid $bg-color;
	&:hover {
		background-color: hover-color($bg-color);
		border-color: hover-color($bg-color);
		color: $text-color;
	}
	&:disabled {
		opacity: $button-disabled-opacity;
	}
}

@mixin button-outline-variant($bg-color, $border-color, $hover-color: $button-outline-hover-color, $text-color: $button-outline-text-color) {
	@include button-base();
	background-color: $bg-color;
	color: $text-color;
	border: 1px solid $border-color;
	&:hover {
		border-color: $hover-color;
		background-color: $bg-color;
		color: $hover-color;
	}
	&:disabled {
		opacity: $button-disabled-opacity;
	}
}

@mixin input($input) {
	height: 44px;
	padding: 8px 16px;
	border: 1px solid $border-color;
	width: 100%;
	border-radius: 8px;
	background-color: #fff;
	font-size: 16px;
	font-weight: 400;
	color: $text-color;
	font-family: $font-family;
}
@mixin input-focus($input-focus) {
	border-color: $color-primary;
}

@mixin select-wrapper($select-wrapper) {
	position: relative;
	select {
		padding-right: 32px;
		appearance: none;
	}
	&::before {
		position: absolute;
		content: '\f078';
		top: 50%;
		right: 12px;
		font-family: 'Font Awesome 5 Free';
		font-size: 12px;
		font-weight: 900;
		color: #858585;
		transform: translateY(-50%);
		z-index: 1;
	}
}

@mixin search-wrapper($search-wrapper) {
	position: relative;
	input {
		padding-right: 32px;
	}
	&::before {
		position: absolute;
		content: '\f002';
		top: 50%;
		right: 12px;
		font-family: 'Font Awesome 5 Free';
		font-size: 12px;
		font-weight: 900;
		color: #858585;
		transform: translateY(-50%);
		z-index: 1;
	}
}

@mixin cover {
	background-repeat: no-repeat;
	background-size: cover;
	background-position: 50% 50%;
}

// TRANSITION
@mixin transition($what) {
	& {
		transition: $what;
	}
}

@mixin hide-text {
	font: 0 / 0 a;
	text-shadow: none;
	color: transparent;
}

//triangle
@mixin arr($width, $height, $bg, $direction) {
	width: 0px;
	height: 0px;
	border-style: solid;
	@if $direction==t {
		border-width: 0 calc($width / 2) + px $height + px calc($width / 2) + px;
		border-color: transparent transparent $bg transparent;
	}
	@if $direction==r {
		border-width: calc($height / 2) + px 0 calc($height / 2) + px $width + px;
		border-color: transparent transparent transparent $bg;
	}
	@if $direction==b {
		border-width: $height + px calc($width / 2) + px 0 calc($width / 2) + px;
		border-color: $bg transparent transparent transparent;
	}
	@if $direction==l {
		border-width: calc($height / 2) + px $width + px calc($height / 2) + px 0;
		border-color: transparent $bg transparent transparent;
	}
	@if $direction==tl {
		border-width: $height + px $width + px 0 0;
		border-color: $bg transparent transparent transparent;
	}
	@if $direction==tr {
		border-width: 0 $width + px $height + px 0;
		border-color: transparent $bg transparent transparent;
	}
	@if $direction==br {
		border-width: 0 0 $height + px $width + px;
		border-color: transparent transparent $bg transparent;
	}
	@if $direction==bl {
		border-width: $width + px 0 0 $height + px;
		border-color: transparent transparent transparent $bg;
	}
}
// example
// +arr(500,50, #000, l)
// +arr(500,50, #000, bl)

@mixin mq($args...) {
	$media-type: 'only screen';
	$media-type-key: 'media-type';
	$args: meta.keywords($args);
	$expr: '';

	@if map.has-key($args, $media-type-key) {
		$media-type: map.get($args, $media-type-key);
		$args: map.remove($args, $media-type-key);
	}

	@each $key, $value in $args {
		@if $value {
			$expr: '#{$expr} and (#{$key}: #{$value})';
		}
	}

	@media #{$media-type} #{$expr} {
		@content;
	}
}
