.portal-sign-up {
	flex-grow: 1;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	overflow: hidden;

	&__wrapper {
		position: relative;
		padding: 60px 0;
		@media (min-width: 992px) {
			padding: 96px 0;
		}
	}
	&__content {
		text-align: center;
		font-weight: 400;
		font-size: 16px;
		line-height: 22px;
		display: flex;
		flex-direction: column;
		gap: 24px;
		@media (min-width: 768px) {
			text-align: left;
		}
	}
}
.sign-up-form {
	border-radius: 20px;
	padding: 40px;
	background: #fff;
	&__title {
		@extend .h3;
		margin-bottom: 16px;
		text-align: center;
	}
	&__subtitle {
		font-weight: 400;
		font-size: 16px;
		line-height: 22px;
		text-align: center;
	}
	&__form {
		margin-top: 32px;
		display: flex;
		flex-direction: column;
		gap: 24px;
	}
	&__separator {
		margin: 32px 0;
		display: flex;
		align-items: center;
		gap: 16px;
		color: $strokes;
		&::before,
		&::after {
			content: '';
			flex-grow: 1;
			height: 1px;
			background: $strokes;
		}
	}

	&__error {
		padding-top: 2px;
		color: $color-danger;
		font-size: 12px;
		display: none;
	}
}
